.list {
  &__item {
    position: relative;
    padding-left: 30px;
    font-size: 20px;
    line-height: 40px;
    color: var(--second-text);

    &::before {
      content: "";
      position: absolute;
      left: 9px;
      top: 50%;
      border-radius: 100%;
      width: 5px;
      height: 5px;
      background-color: var(--accent);
      transform: translateY(-50%);
    }

    @include tablet-min {
      padding-left: 20px;
      font-size: 18px;
      line-height: 30px;
    }
  }
}
