.post-author {
  display: flex;
  align-items: center;
  border: 1px solid #3a3a3a;
  padding: 40px 25px;

  &__image {
    flex-shrink: 0;
    margin-right: 25px;
    width: 124px;
    height: 124px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @include mobile-max {
      margin-right: 0;
      margin-bottom: 25px;
    }
  }

  &__text {
    width: calc(100% - 124px - 25px);

    @include mobile-max {
      width: 100%;
    }
  }

  &__name {
    font-weight: 700;
    font-size: 20px;
    line-height: 140%;
    color: var(--main-text);
  }

  &__position {
    margin-bottom: 8px;
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
    color: var(--main-text);
  }

  &__descr {
    margin: 0;
    max-width: 550px;
    font-weight: 400;
    font-size: 14px;
    line-height: 140%;
    color: var(--second-text);

    a {
      border-bottom: 1px solid transparent;
      color: var(--accent);
      transition: border-bottom 0.3s ease-in-out;

      @include desktop {
        &:hover {
          border-bottom: 1px solid currentColor;
        }
      }
    }
  }

  @include mobile-max {
    flex-direction: column;
    align-items: flex-start;
  }
}

.post-author__descr a.focus-visible {
  &:focus {
    outline: 1px solid var(--accent);
  }
}
