.post-links {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 60px 0;

  &__item {
    display: flex;
    align-items: center;
    font-size: 18px;
    line-height: 25px;
    color: var(--main-text);
    transition: color 0.3s ease-in-out;

    svg {
      width: 80px;
      height: 15px;
      fill: currentColor;
    }

    &--prev {
      svg {
        margin-right: 10px;
      }
    }

    &--next {
      svg {
        margin-left: 10px;
      }
    }

    @include desktop {
      &:hover {
        color: var(--accent);
      }
    }

    @include mobile-max {
      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }
  }

  @include mobile-max {
    flex-direction: column;
    align-items: flex-start;
  }
}
