.more-post {
  color: var(--main-text);
  background-color: var(--more-post);

  &__link {
    display: flex;
    align-items: center;
    color: inherit;
    transition: color 0.3s ease-in-out;
  }

  &__image {
    position: relative;
    flex-shrink: 0;
    width: 170px;
    height: 80px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__category {
    position: absolute;
    left: 0;
    top: 0;
    padding: 3px 12px;
    font-weight: 400;
    font-size: 12px;
    line-height: 160%;
    color: #fff;
    background-color: var(--accent);
  }

  &__text {
    padding: 10px;
    padding-left: 15px;
  }

  &__title {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    font-weight: 400;
    font-size: 12px;
    line-height: 130%;
  }

  @include desktop {
    &:hover {
      color: var(--accent);
    }
  }
}

.more-post__link.focus-visible {
  &:focus {
    outline: 1px solid var(--accent);
  }
}
