.footer {
  &__top {
    padding: 60px 0;
    background-color: var(--use-bg);
  }

  &__info {
    @include tablet-max {
      margin-bottom: 40px;
    }
  }

  &__bottom {
    padding: 12px 15px;
    font-weight: 400;
    font-size: 14px;
    line-height: 19px;
    text-align: center;
    color: var(--main-text);

    a {
      border-bottom: 1px solid currentColor;
      color: inherit;
      transition: color 0.3s ease-in-out;

      @include desktop {
        &:hover {
          color: var(--accent);
        }
      }

      &:active {
        color: var(--accent-active);
      }
    }

    @include mobile-max {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include tablet-max {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 5px;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    color: var(--main-text);
  }

  &__descr {
    margin: 0;
    max-width: 440px;
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: var(--main-text);
  }
}

.footer__bottom a.focus-visible {
  &:focus {
    outline: 1px solid var(--accent);
  }
}
