@import "../mixins";

.theme-switch {
  width: 32px;
  height: 32px;
  color: transparent;
  transition: color 0.3s ease-in-out;

  svg {
    width: 100%;
    height: 100%;
    fill: currentColor;
    object-fit: cover;
    pointer-events: none;
  }

  &__icon {
    display: none;

    &--active {
      display: block;
    }
  }

  @include desktop {
    &:hover {
      color: var(--main-text);
    }
  }

  &:active {
    color: var(--accent-active);
  }

  @include tablet-min {
    margin-right: 0;
    width: 40px;
    height: 40px;
  }
}

.theme-switch.focus-visible {
  &:focus {
    outline: 1px solid var(--accent);
  }
}
