.blog {
  padding-top: 215px;
  padding-bottom: 80px;
}

.blog-list {
  grid-auto-rows: 292px;
  margin-bottom: 80px;

  &__item {
    grid-column: 4 span;

    &:first-child {
      grid-column: 8 span;
      grid-row: 2 span;

      .blog-post__image {
        margin-bottom: 23px;
        height: 496px;

        @include tablet-min {
          height: auto;
        }
      }

      .blog-post__title {
        max-width: 90%;
        font-size: 30px;
        line-height: 130%;
      }

      @include tablet-min {
        grid-column: 4 span;
        grid-row: 1 span;
      }
    }

    &:nth-child(6n + 2) {
      @include desktop {
        .blog-post {
          padding-top: 205px;
          padding-right: 23px;
          padding-left: 23px;
          height: 100%;
        }

        .blog-post__props {
          position: relative;
          z-index: 5;
          color: #fff;
        }

        .blog-post__title {
          position: relative;
          z-index: 5;
          max-width: none;
          color: #fff;
        }

        .blog-post__readtime {
          background-image: url("../img/time-white.svg");
        }

        .blog-post__image {
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;

          &::before {
            content: "";
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            height: 155px;
            background: linear-gradient(180deg, rgba(5, 13, 27, 0) 0%, rgba(5, 13, 27, 0.5) 100%);
          }
        }
      }
    }

    &:nth-child(n+16) {
      display: none;
    }

    @include tablet-min {
      margin-bottom: 38px;

      &:nth-child(n+8) {
        display: none;
      }
    }
  }

  @include tablet-min {
    display: block;
  }
}

.blog-list__item.is-visible {
  display: block;
}

.blog-more {
  display: block;
  border-top: 1px solid #8c8c8c;
  border-bottom: 1px solid #8c8c8c;
  padding: 46px 0;
  width: 100%;
  font-weight: 400;
  font-size: 48px;
  line-height: 65px;
  text-align: center;
  color: var(--main-text);
  transition: background-color 0.3s ease-in-out;

  @include desktop {
    &:hover {
      background-color: var(--accent);
    }
  }

  &:active {
    background-color: var(--accent-active);
  }

  @include mobile-max {
    font-size: 34px;
    line-height: 50px;
  }
}

.blog-more.focus-visible {
  &:focus {
    background-color: var(--accent);
  }
}
