@import "../mixins";

.nav {
  &__list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    @include tablet-min {
      flex-direction: column;
      align-items: flex-end;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 77px;

      @include tablet-min {
        margin-right: 0;
        margin-bottom: 30px;
      }
    }
  }

  &__link {
    font-size: 18px;
    line-height: 32px;
    color: var(--main-text);
    transition: color 0.3s ease-in-out;

    @include desktop {
      &:hover {
        color: var(--accent);
      }
    }

    &:active {
      color: var(--accent-active);
    }

    @include tablet-min {
      font-size: 26px;
      line-height: 44px;
    }
  }

  @include tablet-min {
    position: fixed;
    right: 0;
    top: 0;
    overflow-y: auto;
    padding: 45px;
    padding-top: 170px;
    height: 100vh;
    background-color: var(--main-bg);
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;

    &--active {
      transform: translateX(0);
    }
  }
}

.nav__link.focus-visible {
  &:focus {
    outline: 1px solid var(--accent);
    color: var(--accent);
  }
}
