.contacts {
  display: flex;
  align-items: center;

  &__list {
    display: flex;
    align-items: center;
    margin-right: 75px;

    @include tablet-min {
      margin-right: 0;
      margin-bottom: 40px;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-right: 82px;

      @include mobile-max {
        margin-right: 44px;
      }
    }
  }

  &__link {
    font-weight: 400;
    font-size: 20px;
    line-height: 32px;
    color: var(--main-text);
    transition: color 0.3s ease-in-out;

    @include desktop {
      &:hover {
        color: var(--accent);
      }
    }

    &:active {
      color: var(--accent-active);
    }

    @include desktop-height {
      font-size: 18px;
      line-height: 30px;
    }
  }

  &__donate {
    padding: 3px 14px;
    padding-left: 38px;
    font-family: "NotoSans", sans-serif;
    font-weight: 400;
    font-size: 14px;
    line-height: 32px;
    color: #fff;
    background-image: url("../img/donate.svg");
    background-position: 14px center;
    background-size: 18px 18px;
    background-repeat: no-repeat;
    background-color: var(--accent);
    transition: background-color 0.3s ease-in-out;

    @include desktop {
      &:hover {
        background-color: var(--accent-active);
      }
    }
  }

  @include tablet-min {
    flex-direction: column;
    align-items: flex-start;
  }
}

.contacts__link.focus-visible {
  &:focus {
    outline: 1px solid var(--accent);
    color: var(--accent);
  }
}

.contacts__donate.focus-visible {
  &:focus {
    outline: 1px solid var(--accent);
  }
}
