@import "../mixins";

.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 100;
  padding: 60px 0;
  width: 100%;

  &__logo {
    display: inline-block;
    width: 130px;
    height: 35px;
    color: var(--main-text);
    transition: color 0.3s ease-in-out;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      fill: currentColor;
      object-fit: cover;
    }
  }

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__right {
    display: flex;
    align-items: center;
  }

  &__search {
    margin-right: 75px;

    @include tablet-min {
      margin-right: 30px;
    }
  }

  &__nav {
    margin-right: 74px;

    @include tablet-min {
      margin-right: 0;
    }
  }

  @include desktop-height {
    padding: 30px 0;
  }
}

.logo[href] {
  @include desktop {
    &:hover {
      color: var(--accent);
    }
  }
}
