.dev {
  padding-top: 70px;
  padding-bottom: 110px;

  &__title {
    margin: 0;
    margin-bottom: 40px;
    font-size: 68px;
    line-height: 93px;
    color: var(--main-text);

    @include mobile-max {
      font-size: 34px;
      line-height: 55px;
    }
  }

  &__item {
    grid-column: 6 span;

    @include tablet-max {
      grid-column: 4 span;
    }
  }
}

.dev-link {
  display: inline-block;

  &__title {
    margin: 0;
    margin-bottom: 9px;
    font-weight: 400;
    font-size: 30px;
    line-height: 41px;
    color: var(--accent);
    transition: color 0.3s ease-in-out;

    @include mobile-max {
      font-size: 24px;
      line-height: 34px;
    }
  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 18px;
    line-height: 32px;
    color: var(--main-text);

    @include mobile-max {
      font-size: 16px;
      line-height: 28px;
    }
  }
}

.dev-link {
  @include desktop {
    &:hover {
      .dev-link__title {
        color: var(--accent-active);
      }
    }
  }
}

.footer-bottom {
  display: grid;
  grid-template-columns: 100%;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;

  .header {
    position: initial;
  }
}

.dev-link.focus-visible {
  outline: 1px solid var(--accent);
}
