.search {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1000;
  width: 100%;
  height: 100%;
  background-color: var(--main-bg);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 60px 0;
  }

  &__logo {
    display: inline-block;
    width: 130px;
    height: 35px;
    color: var(--main-text);
    transition: color 0.3s ease-in-out;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      fill: currentColor;
      object-fit: cover;
    }
  }

  &__close {
    display: inline-block;
    width: 32px;
    height: 32px;
    color: var(--main-text);
    transition: color 0.3s ease-in-out;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      stroke: currentColor;
      stroke-width: 2;
      object-fit: cover;
    }

    @include desktop {
      &:hover {
        color: var(--accent);
      }
    }
  }

  &__dropdown {
    display: none;
    padding: 20px;
    padding-right: 10px;
    width: 100%;

    @include mobile-max {
      padding-right: 0;
    }
  }

  .simplebar-scrollbar::before {
    width: 5px;
    background-color: var(--accent);
    opacity: 1;
  }

  // .simplebar-track {
  //   right: -10px;
  // }

  .simplebar-track.simplebar-vertical::before {
    content: "";
    position: absolute;
    right: 6.3px;
    top: 0;
    width: 1px;
    height: 100%;
    background-color: #696969;
  }

  &__form {
    margin: 0 auto;
    padding-top: 120px;
    max-width: 908px;

    @include mobile-max {
      padding-top: 20px;
    }
  }

  &__list {
    max-height: 520px;

    @include tablet-max {
      max-height: 400px;
    }

    @include mobile-max {
      max-height: 350px;
    }
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 20px;

      @include mobile-max {
        margin-bottom: 30px;
      }
    }
  }

  &__input {
    border: 1px solid var(--main-text);
    border-radius: 0;
    padding: 0 50px;
    width: 100%;
    height: 100px;
    font-family: "NotoSans", sans-serif;
    font-weight: 400;
    font-size: 30px;
    line-height: 41px;
    color: var(--main-text);
    background-color: transparent;

    &::placeholder {
      color: var(--main-text);
    }

    @include mobile-max {
      padding: 0 25px;
      height: 70px;
      font-size: 25px;
    }
  }

  &--active {
    opacity: 1;
    visibility: visible;
  }
}

.search__close.focus-visible {
  &:focus {
    outline: 1px solid var(--accent);
  }
}

input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
  display: none;
}

input[type="search"] {
  -webkit-appearance: none;
}

.search-post {
  padding-right: 20px;
  color: var(--main-text);

  &__link {
    display: flex;
    align-items: center;
    padding-right: 150px;
    color: inherit;
    transition: background-color 0.3s;

    @include mobile-max {
      display: block;
      padding-right: 0;
    }
  }

  &__image {
    position: relative;
    flex-shrink: 0;
    width: 170px;
    height: 80px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;

      @include mobile-max {
        display: none;
      }
    }

    @include mobile-max {
      height: auto;
    }
  }

  &__category {
    position: absolute;
    left: 0;
    top: 0;
    padding: 3px 12px;
    font-weight: 400;
    font-size: 12px;
    line-height: 160%;
    color: #fff;
    background-color: var(--accent);

    @include mobile-max {
      position: static;
    }
  }

  &__text {
    padding-right: 10px;
    padding-left: 15px;

    @include mobile-max {
      padding-left: 0;
    }
  }

  &__title {
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
  }

  @include desktop {
    &:hover {
      .search-post__link {
        background-color: var(--use-bg);
      }
    }
  }
}
