.blog-post {
  position: relative;

  &__image {
    position: relative;
    display: block;
    margin-bottom: 12px;
    height: 205px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__category {
    position: absolute;
    left: 0;
    top: 15px;
    padding: 3px 12px;
    font-weight: 400;
    font-size: 12px;
    line-height: 160%;
    color: #fff;
    background-color: var(--accent);
  }

  &__props {
    display: flex;
    align-items: center;
    margin-bottom: 3px;
    font-weight: 400;
    font-size: 12px;
    line-height: 160%;
    color: var(--second-text);
  }

  &__date {
    margin-right: 28px;
  }

  &__readtime {
    padding-left: 29px;
    background-image: url("../img/time.svg");
    background-position: left center;
    background-size: 24px 24px;
    background-repeat: no-repeat;
  }

  &__title {
    position: relative;
    display: -webkit-box;
    overflow: hidden;
    margin: 0;
    max-width: 78%;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    color: var(--main-text);
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;

    @include desktop {
      &:hover {
        color: var(--accent);
      }
    }
  }

  &__link {
    color: inherit;
    transition: color 0.3s ease-in-out;

    &:active {
      outline: none;
      color: var(--accent-active);
    }
  }
}

.blog-post__image.focus-visible {
  &:focus {
    outline: 1px solid var(--accent);
  }
}

.blog-post__link.focus-visible {
  &:focus {
    outline: none;
    color: var(--accent);
  }
}
