.not-found {
  height: 100vh;
  min-height: 750px;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 100%;
  }

  &__left {
    max-width: 665px;

    @include tablet-max {
      display: flex;
      flex-direction: column;
      align-items: center;
      width: 100%;
      max-width: 70%;
      margin: 0 auto;
    }

    @include mobile-max {
      max-width: none;
    }
  }

  &__symbol {
    font-weight: 700;
    font-size: 210px;
    line-height: 110%;
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    background-image: url("../img/not-found-bg.png");
    background-position: center;
    background-size: cover;

    @include mobile-max {
      font-size: 120px;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 400;
    font-size: 30px;
    line-height: 130%;
    color: var(--main-text);

    @include tablet-max {
      font-size: 24px;
      line-height: 40px;
      text-align: center;
    }

    @include mobile-max {
      font-size: 20px;
      line-height: 34px;
      text-align: center;
    }
  }

  &__download {
    display: inline-block;
    padding: 12px 40px;
    font-weight: 700;
    font-size: 20px;
    line-height: 27px;
    color: #fff;
    background-color: var(--accent);
    transition: background-color 0.3s ease-in-out;

    @include desktop {
      &:hover {
        background-color: var(--accent-active);
      }
    }
  }

  &__image {
    transform: translateY(-50px);

    @include desktop-height {
      transform: none;
    }

    @include desktop-height-min {
      transform: none;
    }

    @include desktop-max {
      width: 50%;
    }

    @include tablet-max {
      display: none;
    }
  }

  @include desktop-height {
    min-height: 640px;
  }

  @include desktop-height-min {
    min-height: 600px;
  }
}

.not-found__download.focus-visible {
  &:focus {
    outline: 1px solid var(--accent);
  }
}
