.search-btn {
  width: 32px;
  height: 32px;
  color: var(--main-text);
  transition: color 0.3s ease-in-out;

  svg {
    display: block;
    width: 100%;
    height: 100%;
    fill: transparent;
    stroke: currentColor;
    stroke-width: 2;
    object-fit: cover;
  }

  @include desktop {
    &:hover {
      color: var(--accent);
    }
  }

  &:active {
    color: var(--accent-active);
  }
}

.search-btn.focus-visible {
  &:focus {
    outline: 1px solid var(--accent);
    color: var(--accent);
  }
}
