.post-aside {
  grid-column: 4 span;
  color: #fff;

  @include tablet-max {
    display: none;
  }
}

.post-more {
  position: sticky;
  top: 20px;
  padding: 25px;
  background-color: var(--use-bg);

  &__title {
    margin: 0;
    margin-bottom: 30px;
    font-weight: 400;
    font-size: 18px;
    line-height: 130%;
    color: var(--main-text);
  }

  &__item {
    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }
}
