.post-container {
  padding-top: 215px;
	padding-bottom: 50px;
}

.post {
  grid-column: 8 span;

  &__copy {
    position: absolute;
    right: 24px;
    top: 24px;
    display: flex;
    align-items: center;
    justify-content: center;
    outline: none;
    border-radius: 100%;
    width: 50px;
    height: 50px;
    color: #696969;
    background-color: var(--main-bg);
    transition: color 0.3s ease-in-out;

    svg {
      display: block;
      width: 24px;
      height: 24px;
      stroke: currentColor;
      stroke-width: 2px;
      object-fit: cover;
    }

    @include desktop {
      &:hover {
        color: var(--accent);
      }
    }
  }

  &__top {
    margin-bottom: 38px;
  }

  &__image {
    display: block;
    margin-bottom: 20px;
    width: 100%;
    object-fit: cover;
  }

  &__props {
    display: flex;
    align-items: center;
    margin-bottom: 20px;
    font-weight: 400;
    font-size: 12px;
    line-height: 160%;
    color: var(--second-text);
  }

  &__category {
    margin-right: 26px;
    padding: 3px 12px;
    font-weight: 400;
    font-size: 12px;
    line-height: 160%;
    color: #fff;
    background-color: var(--accent);

    @include mobile-max {
      margin-right: 15px;
    }
  }

  &__date {
    margin-right: 26px;

    @include mobile-max {
      margin-right: 15px;
    }
  }

  &__readtime {
    padding-left: 29px;
    background-image: url("../img/time.svg");
    background-position: left center;
    background-size: 24px 24px;
    background-repeat: no-repeat;
  }

  &__title {
    margin: 0;
    margin-bottom: 40px;
    font-weight: 700;
    font-size: 30px;
    line-height: 130%;
    color: var(--main-text);

    @include mobile-max {
      font-size: 28px;
      line-height: 40px;
    }
  }

  &__descr {
    margin: 0;
    font-weight: 400;
    font-size: 24px;
    line-height: 180%;
    color: var(--main-text);

    @include mobile-max {
      font-size: 20px;
      line-height: 36px;
    }
  }

  &__video {
    margin-bottom: 20px;
    width: 100%;
    height: 496px;

    iframe {
      display: block;
      border: none;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @include mobile-max {
      height: auto;
    }
  }

  @include mobile-max {
    grid-column: 4 span;
  }
}

.post-content {
  overflow-x: hidden;
  padding-bottom: 40px;

  .simplebar-vertical .simplebar-scrollbar::before {
    width: 5px;
    background-color: var(--accent);
    opacity: 1;
  }

  .simplebar-horizontal .simplebar-scrollbar::before {
    height: 5px;
    background-color: var(--accent);
    opacity: 1;
  }

  .simplebar-vertical .simplebar-scrollbar {
    cursor: pointer;
  }

  .simplebar-horizontal .simplebar-scrollbar {
    cursor: pointer;
  }

  p {
    margin: 0;
    margin-bottom: 40px;
    font-weight: 400;
    font-size: 20px;
    line-height: 200%;
    color: var(--main-text);

    @include mobile-max {
      margin-bottom: 30px;
      font-size: 16px;
      line-height: 28px;
    }
  }

  code {
    padding: 3px;
    background-color: var(--code-bg);
  }

  .note {
    margin-bottom: 40px;
    border-left: 2px solid var(--accent);
    padding: 0 28px;
    font-style: italic;
    font-size: 20px;
    line-height: 200%;

    p {
      margin: 0;

      &:not(:last-child) {
        margin-bottom: 20px;
      }
    }

    @include mobile-max {
      padding: 0 14px;
      margin-bottom: 30px;
      font-size: 16px;
      line-height: 28px;
    }
  }

  h2 {
    margin: 0;
    margin-bottom: 20px;
    font-weight: 700;
    font-size: 24px;
    line-height: 200%;
    color: var(--main-text);

    @include mobile-max {
      font-size: 22px;
      line-height: 30px;
    }
  }

  img,
  iframe {
    display: block;
    margin: 30px auto;
    width: auto;
    object-fit: cover;
  }

  iframe {
    border: none;
    width: 100%;
    height: 270px;
  }

  figure {
    margin: 0;
    margin-bottom: 40px;

    img {
      display: block;
      margin: 0;
    }

    figcaption {
      padding: 10px 0;
      font-weight: 400;
      font-size: 20px;
      line-height: 200%;
      text-align: center;
      color: var(--main-text);

      @include mobile-max {
        font-size: 14px;
        line-height: 28px;
      }
    }
  }

  ol,
  ul {
    margin: 0;
    margin-bottom: 40px;
    padding-left: 20px;
    color: var(--main-text);

    li {
      font-size: 20px;
      line-height: 200%;

      &::marker {
        color: var(--accent);
      }

      &:not(:last-child) {
        margin-bottom: 30px;
      }

      @include mobile-max {
        font-size: 16px;
        line-height: 28px;
      }
    }

    @include mobile-max {
      margin-bottom: 30px;
    }
  }

  ul li {
    position: relative;
    list-style: none;

    &::before {
      content: "";
      position: absolute;
      left: -18px;
      top: 17px;
      border-radius: 100%;
      width: 7px;
      height: 7px;
      background-color: var(--accent);

      @include mobile-max {
        top: 11px;
      }
    }
  }

  a {
    border-bottom: 1px solid currentColor;
    color: var(--accent);
    transition: border-bottom 0.3s ease-in-out;

    @include desktop {
      &:hover {
        border-bottom: 1px solid transparent;
      }
    }
  }

  a.focus-visible {
    &:focus {
      outline: 1px solid var(--accent);
    }
  }

  pre {
    overflow-y: auto;
    margin: 0;
    margin-bottom: 40px;
    padding: 25px;
    // max-height: 500px;
    color: var(--main-text);
    background-color: var(--use-bg);

    code {
      display: block;
      padding: 0;
      background-color: transparent;
    }

    code.focus-visible {
      &:focus {
        outline: 1px solid var(--accent);
      }
    }

    @include tablet-min {
      overflow-x: auto;
    }
  }
}

.post-content *:last-child {
  margin-bottom: 0;
}

.tooltip {
  position: relative;
}

.tooltip__btn {
  border: none;
  border-bottom: 1px dashed currentColor;
  padding: 0;
  color: var(--accent);
  background-color: transparent;
  cursor: pointer;

  @include mobile-max {
    border: none;
    color: var(--main-text);
  }
}

.tooltip__txt {
  position: absolute;
  left: 0;
  top: calc(100% + 5px);
  z-index: 3;
  padding: 20px;
  width: max-content;
  max-width: 370px;
  box-shadow: 0 0 29px rgba(0, 0, 0, 0.32);
  font-weight: 400;
  font-size: 14px;
  line-height: 200%;
  color: var(--main-text);
  background: var(--use-bg);
  opacity: 0;
  visibility: hidden;
  transform: translateY(10px);
  transition: opacity 0.3s ease-in-out, transform 0.3s ease-in-out, visibility 0.3s ease-in-out;

  @include tablet-max {
    left: auto;
    right: 0;
  }

  @include tablet-min {
    left: 0;
    right: auto;
  }

  @include mobile-max {
    display: none;
  }
}

.tooltip__btn:hover + .tooltip__txt {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.tooltip__btn:focus {
  outline: 1px solid currentColor;
}

.tooltip__btn:focus + .tooltip__txt {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.post-bottom {
  display: flex;
  justify-content: space-between;
  padding-top: 40px;
  padding-bottom: 80px;

  &__left {
    max-width: 400px;

    @include mobile-max {
      margin-bottom: 40px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 15px;
    font-weight: 400;
    font-size: 30px;
    line-height: 130%;
    color: var(--main-text);
  }

  &__link {
    font-weight: 400;
    font-size: 20px;
    line-height: 140%;
    color: var(--accent);
    transition: color 0.3s ease-in-out;

    @include desktop {
      &:hover {
        color: var(--accent-active);
      }
    }
  }

  &__text {
    font-weight: 400;
    font-size: 14px;
    line-height: 24px;
    color: var(--second-text);
  }

  @include mobile-max {
    flex-direction: column;
    align-items: flex-start;
  }
}

.telegram {
  display: inline-block;
  margin-bottom: 5px;
  font-weight: 700;
  font-size: 30px;
  line-height: 41px;
  color: var(--accent);

  &__text {
    font-weight: 400;
    font-size: 18px;
    line-height: 25px;
    color: var(--main-text);
    transition: color 0.3s ease-in-out;
  }

  @include desktop {
    &:hover {
      .telegram__text {
        color: var(--accent);
      }
    }
  }
}

.post-bottom__link.focus-visible {
  &:focus {
    outline: 1px solid var(--accent);
  }
}

.telegram.focus-visible {
  &:focus {
    outline: 1px solid var(--accent);
  }
}

.social-share {
  display: flex;
  align-items: center;

  &__item {
    &:not(:last-child) {
      margin-right: 25px;
    }

    &:last-child {
      .social-share__btn {
        width: 32px;
        height: 32px;
      }
    }
  }

  &__btn {
    display: block;
    width: 25px;
    height: 25px;
    color: #c4c4c4;
    transition: color 0.3s ease-in-out;

    svg {
      display: block;
      width: 100%;
      height: 100%;
      fill: currentColor;
      object-fit: cover;
    }

    @include desktop {
      &:hover {
        color: var(--accent);
      }
    }
  }
}

.social-share__btn.focus-visible {
  &:focus {
    outline: 1px solid var(--accent);
  }
}

.post-sources {
  margin-bottom: 40px;
  padding: 20px;
  font-size: 16px;
  line-height: 140%;
  color: var(--main-text);
  background-color: var(--use-bg);

  &__link {
    display: inline-flex;
    align-items: center;
    margin-left: 10px;
    border-bottom: 1px solid transparent;
    color: var(--accent);
    transition: border-bottom 0.3s ease-in-out;

    svg {
      margin-right: 3px;
      width: 15px;
      height: 15px;
      fill: currentColor;
    }

    @include desktop {
      &:hover {
        border-bottom: 1px solid currentColor;
      }
    }
  }
}

.post-sources__link.telegram.focus-visible {
  &:focus {
    outline: 1px solid var(--accent);
  }
}

.post__copy.focus-visible {
  outline: 1px solid var(--accent);
}
