.post {
  --main-color: #c5c8c6;
  --main-shadow: 0 1px rgba(0, 0, 0, 0.3);
  --comment-color: #7c7c7c;
  --punctuation-color: #c5c8c6;
  --tag-color: #96cbfe;
  --class-name-color: #ffffb6;
  --constant-color: #9c9;
  --deleted-color: #f92672;
  --number-color: #ff73fd;
  --selector-color: #a8ff60;
  --variable-color: #c6c5fe;
  --operator-color: #ededed;
  --entity-color: #ffffb6;
  --url-color: #96cbfe;
  --string-color: #87c38a;
  --attr-value-color: #f9ee98;
  --function-color: #dad085;
  --regex-color: #e9c062;
  --important-color: #fd971f;
}

.light-theme {
  .post {
    --main-color: #728fcb;
    --main-shadow: none;
    --comment-color: #b6ad9a;
    --punctuation-color: #b6ad9a;
    --tag-color: #063289;
    --class-name-color: #b29762;
    --constant-color: #b29762;
    --deleted-color: #728fcb;
    --number-color: #063289;
    --selector-color: #2d2006;
    --variable-color: #93abdc;
    --operator-color: #063289;
    --entity-color: #728fcb;
    --url-color: #728fcb;
    --string-color: #063289;
    --attr-value-color: #728fcb;
    --function-color: #b29762;
    --regex-color: #728fcb;
    --important-color: #896724;
  }
}

code[class*="language-"],
pre[class*="language-"] {
  color: var(--main-color);
  text-shadow: var(--main-shadow);
  font-family: Inconsolata, Monaco, Consolas, 'Courier New', Courier, monospace;
  direction: ltr;
  text-align: left;
  white-space: pre;
  word-spacing: normal;
  word-break: normal;
  line-height: 1.5;

  -moz-tab-size: 2;
  -o-tab-size: 2;
  tab-size: 2;

  -webkit-hyphens: none;
  -moz-hyphens: none;
  -ms-hyphens: none;
  hyphens: none;
}

/* Code blocks */
pre[class*="language-"] {
  overflow: auto;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
  color: var(--comment-color);
}

.token.punctuation {
  color: var(--punctuation-color);
}

.namespace {
  opacity: 0.7;
}

.token.property,
.token.keyword,
.token.tag {
  color: var(--tag-color);
}

.token.class-name {
  color: var(--class-name-color);
  text-decoration: underline;
}

.token.boolean,
.token.constant {
  color: var(--constant-color);
}

.token.symbol,
.token.deleted {
  color: var(--deleted-color);
}

.token.number {
  color: var(--number-color);
}

.token.selector,
.token.attr-name,
.token.string,
.token.char,
.token.builtin,
.token.inserted {
  color: var(--selector-color);
}

.token.variable {
  color: var(--variable-color);
}

.token.operator {
  color: var(--operator-color);
}

.token.entity {
  color: var(--entity-color);
  cursor: help;
}

.token.url {
  color: var(--url-color);
}

.language-css .token.string,
.style .token.string {
  color: var(--string-color);
}

.token.atrule,
.token.attr-value {
  color: var(--attr-value-color);
}

.token.function {
  color: var(--function-color);
}

.token.regex {
  color: var(--regex-color);
}

.token.important {
  color: var(--important-color);
}

.token.important,
.token.bold {
  font-weight: bold;
}

.token.italic {
  font-style: italic;
}
